import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import { bunMasters } from './jobs/bunMasters'
import { yugle } from './jobs/yugle'
import { currentJobContainer } from './jobs/job'
import { business } from './library/business/business'
import { selfImprovement } from './library/selfImprovement/selfImprovement'
import { energyStat } from './stats/energy'
import { studyPointsPerHourStat } from './stats/studyPointsPerHour'
import { workExperiencePerHourStat } from './stats/workExperiencePerHour'
import { dailyExpensesStat } from './stats/dailyExpenses'
import { dailyIncomeStat } from './stats/dailyIncome'
import { dailyNetIncomeStat } from './stats/dailyNetIncome'
import { schedule, activities } from './schedule/schedule'
import { saver } from './save'

const fields = [selfImprovement, business]
const careers = [bunMasters, yugle]
console.log(fields)

const timeConfig = {
  ticksPerDay: 1,
  tickLength: 20,
  sessionStart: 0,
  sessionTicks: 0
}

const currency = {
  money: 0,
  studyPoints: 20
}

const time = {
  currentDay: 1,
  currentTick: 0,
  currentTime: 0,
  lastTime: 0
}

const stats = {
  energy: energyStat,
  studyPointsPerHour: studyPointsPerHourStat,
  workExperiencePerHour: workExperiencePerHourStat,
  dailyExpenses: dailyExpensesStat,
  dailyIncome: dailyIncomeStat,
  dailyNetIncome: dailyNetIncomeStat
}

const state = {
  currency: currency,
  time: time,
  stats: stats,
  careers: careers,
  schedule: schedule,
  activities: activities,
  timeConfig: timeConfig,
  currentJob: careers[0].jobs[0],
  fields: fields,
  currentActivity: activities[0]
}

const getters = {
  currency: state => state.currency,
  time: state => state.time,
  stats: state => state.stats,
  timeConfig: state => state.timeConfig,
  reverseSchedule: state => state.schedule.timetable.slice().reverse(),
  schedule: state => state.schedule,
  activities: state => state.activities,
  careers: state => state.careers,
  fields: state => state.fields,
  jobs: state => state.jobs,
  currentActivity: state => state.currentActivity
}

let advanceScheduleCursor = function (state, scheduleCursor, until) {
  while (scheduleCursor < until) {
    let activityTime = state.schedule.lookupCurrentActivity(scheduleCursor, true)
    let activity = activityTime[0]
    let doneFor = Math.min(activityTime[1], until - scheduleCursor)
    activity.do(state, doneFor)
    scheduleCursor += doneFor
  }
}

let advanceSchedule = function (state) {
  let scheduleCursor = state.time.currentTime
  state.time.currentTick += 1
  if (state.time.currentTick >= state.timeConfig.ticksPerDay) {
    if (state.time.currentDay % 10 === 0) {
      saver.save()
    }
    state.currency.money += state.stats.dailyNetIncome.effective
    state.time.currentTick = 0
    state.time.currentDay += 1
    advanceScheduleCursor(state, scheduleCursor, 24)
    scheduleCursor = 0
  }
  state.time.currentTime = state.time.currentTick / state.timeConfig.ticksPerDay * 24
  state.time.lastTime += state.timeConfig.tickLength
  state.currentActivity.isCurrent = false
  state.currentActivity = state.schedule.lookupCurrentActivity(state.time.currentTime)
  state.currentActivity.isCurrent = true
  advanceScheduleCursor(state, scheduleCursor, state.time.currentTime)
}

let tickTime = function (state, ticks) {
  while (ticks > 0) {
    ticks -= 1
    state.time.sessionTicks += 1
    advanceSchedule(state)
  }
}

const findActiveJob = function (state) {
  for (let career of state.careers) {
    for (let job of career.jobs) {
      if (job.state.active) {
        return job
      }
    }
  }
}

// define the possible mutations that can be applied to our state
const mutations = {
  TICK (state, ticks) {
    tickTime(state, ticks)
  },
  INITIALIZE_TIMING (state) {
    let dateObject = new Date()
    state.time.lastTime = dateObject.getTime()
    state.time.sessionStart = state.time.lastTime
    state.currentJob = findActiveJob(state)
    currentJobContainer.setCurrentJob(state.currentJob)
    saver.load()
    state.currentJob = findActiveJob(state)
    currentJobContainer.setCurrentJob(state.currentJob)
  },
  CHANGE_ACTIVITY_TIME (state, activityDelta) {
    let activity = activityDelta[0]
    let delta = activityDelta[1]
    let freeTime = state.activities[state.activities.length - 1]
    delta = Math.min(delta, freeTime.state.duration) // upper limit: available free time
    delta = Math.max(delta, -activity.state.duration) // lower limit: remaining activity duration
    activity.state.duration += delta
    freeTime.state.duration -= delta
    state.schedule.update()
  },
  SET_ACTIVE_JOB (state, job) {
    state.currentJob.deactivate()
    job.activate()
    state.currentJob = job
    currentJobContainer.setCurrentJob(job) // to notify entities which depend on the current job
  },
  LEARN (state, subject) {
    if (subject.studyPoints <= state.currency.studyPoints && subject.locks.allOpen) {
      subject.learn()
      state.currency.studyPoints -= subject.studyPoints
    }
  },
  HARD_RESET (state) {
    window.localStorage.clear()
    window.location.reload()
  }
}

const actions = {
  tick: ({ commit }, ticks) => {
    commit('TICK', ticks)
  },
  initializeTiming: ({ commit }) => {
    commit('INITIALIZE_TIMING')
  },
  changeActivityTime: ({ commit }, activityDelta) => {
    commit('CHANGE_ACTIVITY_TIME', activityDelta)
  },
  setActiveJob: ({ commit }, job) => {
    commit('SET_ACTIVE_JOB', job)
  },
  learn: ({ commit }, subject) => {
    commit('LEARN', subject)
  },
  hardReset: ({ commit }) => {
    commit('HARD_RESET')
  }
}

// create the Vuex instance by combining the state and mutations objects
// then export the Vuex store for use by our components
export default new Vuex.Store({
  state: state,
  getters: getters,
  mutations: mutations,
  actions: actions
})



