import { Stat } from './stat'
import { standardEnergyModifier } from '../modifiers/standardEnergy'

class StudyPointsPerHourStat extends Stat {
  constructor () {
    super('sppH', 'Study Points/h', 1.0)
    this.addModifier(standardEnergyModifier)
    this.update()
  }
}

export const studyPointsPerHourStat = new StudyPointsPerHourStat()



