import { leadership101 } from '../library/business/leadership'
import { Job, jobExpHelper } from './job'
import { Career } from './career'
import { createLevelLock, createCompletedLock } from '../locks.js'

class BurgerFlipper extends Job {
  constructor () {
    super('bm1', 'Burger Flipper', 5, true)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(0.25) * level
  }
}

const burgerFlipper = new BurgerFlipper()

class ChiefFlipper extends Job {
  constructor () {
    super('bm2', 'Chief Flipper', 6)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(0.75) * level
  }
}

const chiefFlipper = new ChiefFlipper()

class KitchenManager extends Job {
  constructor () {
    super('bm3', 'Kitchen Manager', 8)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(1.0) * level
  }
}

const kitchenManager = new KitchenManager()

class ShiftManager extends Job {
  constructor () {
    super('bm4', 'Shift Manager', 15)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(3) * level
  }
}

const shiftManager = new ShiftManager()

class AssistantManager extends Job {
  constructor () {
    super('bm5', 'Assistant Manager', 20)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(10) * level
  }
}

const assistantManager = new AssistantManager()

class RestaurantManager extends Job {
  constructor () {
    super('bm6', 'Restaurant Manager', 30)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(15) * level
  }
}

const restaurantManager = new RestaurantManager()

class RegionalManager extends Job {
  constructor () {
    super('bm7', 'Regional Manager', 50)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(30) * level
  }
}

const regionalManager = new RegionalManager()

class ChiefStrategist extends Job {
  constructor () {
    super('bm8', 'RegionalManager', 100)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(40) * level
  }
}

const chiefStrategist = new ChiefStrategist()

class CEO extends Job {
  constructor () {
    super('bm9', 'CEO', 250)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(50) * level
  }
}

const ceo = new CEO()

createLevelLock(burgerFlipper, chiefFlipper, 10)
createLevelLock(chiefFlipper, kitchenManager, 10)
createCompletedLock(leadership101, shiftManager)
createLevelLock(kitchenManager, shiftManager, 10)
createLevelLock(shiftManager, assistantManager, 10)
createLevelLock(assistantManager, restaurantManager, 10)
createLevelLock(restaurantManager, regionalManager, 10)
createLevelLock(regionalManager, chiefStrategist, 10)
createLevelLock(chiefStrategist, ceo, 10)

export const bunMasters = new Career('BunMasters', [burgerFlipper, chiefFlipper, kitchenManager, shiftManager, assistantManager, restaurantManager, regionalManager, chiefStrategist])



