import { gungHo } from '../library/business/leadership'
import { Job, jobExpHelper } from './job'
import { Career } from './career'
import { createLevelLock, createCompletedLock } from '../locks.js'

class UnpaidIntern extends Job {
  constructor () {
    super('yugle1', 'Unpaid Intern', 0)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(0.5) * level
  }
}

const unpaidIntern = new UnpaidIntern()

class JuniorDeveloper extends Job {
  constructor () {
    super('yugle2', 'Junior Developer', 7)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(1) * level
  }
}

const juniorDeveloper = new JuniorDeveloper()

class SoftwareEngineer extends Job {
  constructor () {
    super('yugle3', 'Software Engineer', 12)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(5) * level
  }
}

const softwareEngineer = new SoftwareEngineer()

class ProjectLead extends Job {
  constructor () {
    super('yugle4', 'Project Lead', 16)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(10) * level
  }
}

const projectLead = new ProjectLead()

class TeamLead extends Job {
  constructor () {
    super('yugle5', 'Team Lead', 25)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(15) * level
  }
}

const teamLead = new TeamLead()

class SeniorArchitect extends Job {
  constructor () {
    super('yugle6', 'Senior Architect', 30)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(20) * level
  }
}

const seniorArchitect = new SeniorArchitect()

class DivisionLead extends Job {
  constructor () {
    super('yugle7', 'Division Lead', 40)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(25) * level
  }
}

const divisionLead = new DivisionLead()

class ProductManager extends Job {
  constructor () {
    super('yugle8', 'ProductManager', 60)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(30) * level
  }
}

const productManager = new ProductManager()

class CTO extends Job {
  constructor () {
    super('yugle9', 'CTO', 100)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(40) * level
  }
}

const cto = new CTO()

class CEO extends Job {
  constructor () {
    super('yugle10', 'CEO', 250)
  }

  nextLevelAtExp (level) {
    return jobExpHelper(80) * level
  }
}

const ceo = new CEO()

createLevelLock(unpaidIntern, juniorDeveloper, 10)
createLevelLock(juniorDeveloper, softwareEngineer, 10)
createLevelLock(softwareEngineer, projectLead, 10)
createCompletedLock(gungHo, teamLead)
createLevelLock(projectLead, teamLead, 10)
createLevelLock(teamLead, seniorArchitect, 10)
createLevelLock(seniorArchitect, divisionLead, 10)
createLevelLock(divisionLead, productManager, 10)
createLevelLock(productManager, cto, 10)
createLevelLock(cto, ceo, 10)

export const yugle = new Career('Yugle', [unpaidIntern, juniorDeveloper, softwareEngineer, projectLead, teamLead, seniorArchitect, divisionLead, productManager, cto, ceo])



