<template>
<table>
      <tr v-for="activity in activities" :style="{ backgroundColor: activity.isCurrent ? '#DDD' : '#FFF' }">
          <td style='width:5rem'>{{ activity.name }}</td>
          <td style='width:3rem'>{{ activity.state.duration }}</td>
          <td style='width:1rem'><button v-on:click="changeActivityTime([activity, 1])" v-if="activity.durationByUser">+</button></td>
          <td style='width:1rem'><button v-on:click="changeActivityTime([activity, -1])" v-if="activity.durationByUser">-</button></td>
      </tr>
</table>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'schedule-controller',
  computed: mapGetters(['activities']),
  methods: mapActions(['changeActivityTime'])
}
</script>



