import { IncomePerWorkHourStat } from '../stats/incomePerWorkHour'
import { BasicEntity } from '../basicEntity.js'

export class Job extends BasicEntity {
  constructor (id, name, basePay, active = false) {
    const state = new JobState()
    state.active = active
    super(id, name, state)
    this.basePay = basePay
    this.income = new IncomePerWorkHourStat(this)
    this.update()
  }

  activate () {
    this.state.active = true
  }

  deactivate () {
    this.state.active = false
  }

  nextLevelAtExp (level) {
    return 1
  }

  gainExperience = function (state, duration) {
    let jobState = this.state
    jobState.experience += state.stats.workExperiencePerHour.effective * duration
    let levelUp = false
    while (jobState.experience >= this.nextLevelAtExp(jobState.level)) {
      levelUp = true
      jobState.experience -= this.nextLevelAtExp(jobState.level)
      jobState.level += 1
    }
    if (levelUp) {
      this.update()
    }
  }
}

export class JobState {
  constructor () {
    this.level = 1
    this.experience = 0
    this.active = false
    this.unlocked = false
  }
}

class CurrentIncomeContainer extends BasicEntity {
  constructor () {
    super('currentIncomeContainer', 'Current Income Container', {})
  }

  setCurrentIncome (income) {
    this.income = income
    income.dependants.push(this)
    this.update()
  }

  update () {
    super.update()
  }
}

export const currentIncomeContainer = new CurrentIncomeContainer()

class CurrentJobContainer extends BasicEntity {
  constructor () {
    super('currentJobContainer', 'Current Job Container', {})
  }

  setCurrentJob (job) {
    console.log(job)
    this.job = job
    currentIncomeContainer.setCurrentIncome(job.income)
    super.update()
  }
}

export const currentJobContainer = new CurrentJobContainer()

// returns job experience increments which result in a certain time until promotion
export const jobExpHelper = function (years) {
  // assume 8 hour work day, 1 exp/ h. 8 exp per day, 2920 per year
  // experience needed to level 10 = 5.5 * 10 * baseline = 55 * baseline
  // years = exp / 2920 = baseline * 55 / 2920
  // baseline = years * 2920 / 55 = years * 53
  return years * 53
}



