class Saver {
  constructor () {
    this.registeredObjects = {}
  }

  register (basicEntity) {
    this.registeredObjects[basicEntity.id] = basicEntity
  }

  save () {
    const state = {}
    for (let key in this.registeredObjects) {
      state[key] = this.registeredObjects[key].state
    }
    window.localStorage.setItem('life_idle_save', JSON.stringify(state))
  }

  load () {
    let savedState = window.localStorage.getItem('life_idle_save')
    if (!savedState) {
      return
    } else {
      let parsedState = JSON.parse(savedState)
      for (let key in this.registeredObjects) {
        if (parsedState.hasOwnProperty(key)) {
          this.registeredObjects[key].state = parsedState[key]
        }
      }
    }
    for (let key in this.registeredObjects) {
      this.registeredObjects[key].update()
    }
  }
}

export const saver = new Saver()



