import { BasicEntity } from '../basicEntity'

class ActivityState {
  constructor (duration) {
    this.duration = duration
  }
}

class Activity extends BasicEntity {
  constructor (id, name, ingName, duration, durationByUser, color) {
    super(id, name, new ActivityState(duration))
    this.ingName = ingName
    this.durationByUser = durationByUser
    this.color = color
    this.isCurrent = false
    this.update()
  }

  do (state, duration) {
    return
  }
}

class Sleep extends Activity {
  constructor () {
    super('activitySleep', 'Sleep', 'sleeping', 8, false, '#007D43')
  }

  do (state, duration) {
    return
  }
}

export const sleep = new Sleep()

class Work extends Activity {
  constructor () {
    super('activityWork', 'Work', 'working', 8, true, '#F6768E')
  }

  do (state, duration) {
    state.currentJob.gainExperience(state, duration)
  }
}

export const work = new Work()

class Study extends Activity {
  constructor () {
    super('activityStudy', 'Study', 'studying', 1, true, 'FF7A5C')
  }

  do (state, duration) {
    state.currency.studyPoints += state.stats.studyPointsPerHour.effective * duration
  }
}

export const study = new Study()

class Eat extends Activity {
  constructor () {
    super('activityEat', 'Eat', 'eating', 3, false, '#00538A')
  }

  do (state, duration) {
    return
  }
}

export const eat = new Eat()

class Chores extends Activity {
  constructor () {
    super('activityChores', 'Chores', 'Cleaning', 2, false, '#000000')
  }
}

export const chores = new Chores()

class Slacking extends Activity {
  constructor () {
    super('activitySlacking', 'Slacking', 'slacking', 1, false, '#000000')
  }
}

export const slacking = new Slacking()

class FreeTime extends Activity {
  constructor () {
    super('activityFreeTime', 'Free Time', 'slacking', 1, false, '#53377A')
  }
}

export const freeTime = new FreeTime()



