<template>
  <div>
    <tr>
          <td>
            <button type="button" :class="['btn', subject.state.completed ? 'btn-success' : 'btn-secondary']"
              :disabled="!subject.locks.allOpen" :title="subject.locks.lockStrings()"
              v-on:click="learn(subject)" style="width: 100%">{{ subject.locks.allOpen ? subject.name : '?' }}</button>
          </td>
          <td>
            {{ subject.studyPoints }}
          </td>
          <td>
            {{ subject.description }}
          </td>
      </tr>
      <tr>
        blabla
      </tr>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'subject-button',
  props: ['subject'],
  methods: mapActions(['learn']),
  computed: mapGetters(['currency'])
}
</script>



