import { Modifier } from './modifier'
import { doubleIncomeAtLevelStat } from '../stats/doubleIncomeAtLevel'

export class JobLevelModifier extends Modifier {
  constructor (job) {
    super(job.id + 'jobLevelMod', 'Job Level', 10)
    this.job = job
    job.dependants.push(this)
  }

  update () {
    this.state.factor = 1 + Math.log(this.job.state.level) / Math.log(doubleIncomeAtLevelStat.effective)
    super.update()
  }

  apply (base) {
    return base * this.state.factor
  }
}



